<template>
  <div id="app">
    <Editor v-if="xmlJsonString" :iiifUrl="iiifUrl" :xmlJson="toJsonObject" />
    <Editor v-else :iiifUrl="iiifUrl" :xmlJson="xmlJson" />
  </div>
</template>

<script>
// Test page : http://localhost:5000/collection/76950/doc/497355/detail/1

import Editor from "./views/Editor.vue";

const json = require("./assets/page1.json");

export default {
  name: "app",
  props: {
    iiifUrl: {
      type: String,
      default: () =>
        "https://rhus-209.man.poznan.pl/fcgi-bin/iipsrv.fcgi?IIIF=11/8//2020601/https___1914_1918_europeana_eu_contributions_21890/21890.261095.original.tif/info.json",

    },
    xmlJson: {
      type: Object,
      default: () => json,
    },
    xmlJsonString: {
      type: String,
    },
    tags: {
      type: Object,
    },
  },
  methods: {
    escapeSpecialChars(string) {
      return string
        .replace(/\\n/g, "\\n")
        .replace(/\\'/g, "\\'")
        .replace(/\\"/g, '\\"')
        .replace(/\\&/g, "\\&")
        .replace(/\\r/g, "\\r")
        .replace(/\\t/g, "\\t")
        .replace(/\\b/g, "\\b")
        .replace(/\\f/g, "\\f");
    },
  },
  components: {
    Editor,
  },
  computed: {
    toJsonObject() {
      var cleanedString = this.escapeSpecialChars(this.xmlJsonString);
      const obj = JSON.parse(cleanedString);
      return obj;
    },
  },
  mounted() {
    if (this.tags) {
      this.$store.dispatch("browse/setTagDefs", { tagDefs: this.tags });
    }
  },
};
</script>
