import { Node, mergeAttributes } from "@tiptap/core";
import { VueNodeViewRenderer } from "@tiptap/vue-2";
import Component from "./LineNode.vue";
import { TextSelection } from "prosemirror-state";
import { Plugin, PluginKey } from "prosemirror-state";

export default Node.create({
  name: "line",

  group: "block+",

  // content: '(inline* | tag*)',
  content: "inline* ",
  addAttributes() {
    return {
      line: {
        default: [],
      },
    };
  },
  addProseMirrorPlugins() {
    return [
      new Plugin({
        props: {
          handleKeyDown: (view, event) => {
            if (event.which === 8 || event.which === 46) {
              view.state.deleting = true;
            }

            return false;
          },
        },

        filterTransaction: (transaction, state) => {
          var isDeleting = state.deleting;

          if (!state.deleting) {
            return true;
          }

          var result = true;

          transaction.mapping.maps.forEach((map) => {
            map.forEach((oldStart, oldEnd, newStart, newEnd) => {
              state.doc.nodesBetween(
                oldStart,
                oldEnd,
                (node, number, pos, parent, index) => {
                  if (node.type.name === "line") {
                    result = false;
                  }
                }
              );
            });
          });

          return result;
        },
      }),
    ];
  },
  addKeyboardShortcuts() {
    return {
      Enter: (state, dispatch, view) => {
        this.editor.chain().focus().selectNodeForward().run();
        // this.editor.commands.selectNodeForward()
        if (this.editor.state.selection.node) {
          var lineId =
            this.editor.state.selection.node.attrs.line.attributes.id;
          const el = document.getElementById(lineId);
          el.scrollIntoView({ behavior: "smooth" });
        }

        return true;
      },
    };
  },

  parseHTML() {
    return [
      {
        tag: "div",
      },
    ];
  },

  renderHTML({ HTMLAttributes }) {
    return ["div", mergeAttributes(HTMLAttributes)];
  },

  addNodeView() {
    return VueNodeViewRenderer(Component);
  },
});
